.btn_container {
  display: flex;
  justify-content: center;
  margin: 5px 0;
  gap: 10px;
}

.btn_container button {
  font-size: 15px;
  border: none;
  background-color: var(--main-color);
  border-radius: 10px;
  color: white;
  padding: 10px;
  cursor: pointer;
  transition: ease 0.5s;
}

.btn_container button:hover {
  background-color: var(--secondary-color);
  transition: ease 0.5s;
}

.btn_container button.active {
  background-color: #dfd2d2;
  color: black;
  cursor: default;
}

.qtybtn_container {
  display: flex;
  justify-content: center;
  gap: 0 20px;
  margin: 10px 0;
}

.qtybtn_container button {
  font-size: 25px;
  border: none;
  text-align: center;
  align-items: center;
  display: flex;
  border-radius: 5px;
  background-color: var(--main-color);
  color: #ffff;
  cursor: pointer;
  transition: ease-in-out 0.2s;
}

.qtybtn_container button:hover {
  transform: scale(1.2);
  transition: ease-in-out 0.2s;
}

.qtybtn_container span {
  background-color: #dfd2d2;
  padding: 5px 10px;
  border-radius: 5px;
  font-size: 20px;
}
