.reviews_container {
  background: var(--background-gradient);
  height: 100%;
  width: 100%;
  z-index: 0;
  position: relative;
  padding-top: 40px;
}

.reviews_container p {
  text-align: center;
  margin: 0;
  font-size: 50px;
  color: #ffff;
}

.reviews_content1 {
  width: 100%;
}

.reviews_gallery1 {
  padding: 20px;
}

.reviews_gallery1 img {
  width: 100%;
  height: auto;
  border-radius: 10px;
}
.reviews_content2 {
  padding: 20px;
}

.reviews_gallery2 img {
  width: 100%;
  height: auto;
  border-radius: 10px;
}

.reviews_container .swiper-button-next,
.reviews_container .swiper-button-prev {
  background: tomato;
  padding: 10px 15px;
  border-radius: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #ffff;
}

.reviews_container .swiper-pagination-bullet {
  padding: 5px;
}
.reviews_container .swiper-pagination-bullet-active {
  background-color: rgb(206, 6, 6);
}

@media (max-width: 550px) {
  .reviews_container .swiper-button-next,
  .reviews_container .swiper-button-prev {
    position: absolute;
    top: 80%;
    background: none;
    margin: 0;
  }
}
@media (max-width: 375px) {
  .reviews_container .swiper-button-next,
  .reviews_container .swiper-button-prev {
    display: none;
  }
}
