.footer {
  background: var(--background-gradient);
  position: relative;
  z-index: 1;
  box-shadow: 0 -5px 50px rgb(0 50 0 / 0.2)

}

.footer_content {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  justify-content: space-evenly;
}

#title {
  color: #fff;
  font-size: 30px;
}

#subtitle {
  color: #fff;
  font-size: 20px;
  display: flex;
  justify-content: center;
  text-align: center;
  gap: 10px;
}

#logo {
  color: var(--main-color);
  font-size: 25px;
}

.footer_location a {
  text-decoration: none;
  margin: 15px 0;
}

.footer_location a:hover {
  text-decoration: underline;
}
.footer_location {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.footer_contact {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.footer_social {
  display: flex;
  flex-direction: column;
  gap: 20px;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.footer_social a {
  border: none;
  display: flex;
  background: none;
  font-size: 40px;
  color: #fff;
  cursor: pointer;
  transition: all .2s;
}

.footer_social a:hover {
    transform: scale(1.1);
}

.social_logo {
  display: flex;
  width: 100%;
  height: 100%;
  gap: 40px;
  justify-content: center;
}
