.checkout {
  position: fixed;
  background: var(--background-gradient);
  z-index: 5;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  display: flex;
  flex-wrap: wrap;
  gap: 40px 0px;
  justify-content: space-evenly;
  align-items: center;
  overflow: auto;
}

.checkout_card {
  padding: 20px;
  min-width: 270px;
  max-width: 500px;
  min-height: 660px;
  width: 100%;
  background-color: #ffff;
  text-align: start;
  border-radius: 30px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.checkout_card p {
  font-size: 30px;
}

.solid_border {
  border: 1px solid white;
  background-color: black;
  height: 1px;
  border-radius: 2px;
}
.checkout_header {
  display: flex;
  width: 100%;
  justify-content: space-between;
}
.checkout_header p {
  font-size: 20px;
  font-weight: bold;
  width: 100%;
  text-align: center;
}
.checkout_items {
  display: flex;
  flex-direction: column;
}

.checkout_items #item {
  display: flex;
  width: 100%;
  justify-content: space-between;
}
.checkout_items #item p {
  font-size: 20px;
  width: 100%;
  text-align: center;
}

.checkout_total {
  display: flex;
  width: 100%;
  justify-content: space-between;
}
.checkout_total p {
  font-size: 30px;
  font-weight: bolder;
}

.checkout_total #total {
  font-size: 30px;
  text-decoration: underline;
}

.checkout_buttons {
  display: flex;
  width: 100%;
  gap: 20px;
  justify-content: end;
}
.checkout_buttons button {
  border: none;
  padding: 20px;
  background-color: var(--main-color);
  color: #ffff;
  border-radius: 20px;
  cursor: pointer;
  transition: all 0.4s;
}

.checkout_buttons button:nth-child(2) {
  width: 420px;
}

.checkout_buttons button:hover {
  transform: scale(1.1);
}

.details_card {
  padding: 20px;
  min-width: 270px;
  max-width: 500px;
  min-height: 660px;
  width: 100%;
  background-color: #ffff;
  border-radius: 30px;
}

.details_card p {
  font-size: 30px;
}

.method_buttons p {
  font-size: 20px;
  margin: 0;
}

.method_buttons {
  display: flex;
  width: 100%;
  gap: 0 20px;
  margin: 12px 0;
  align-items: center;
}

.method_buttons button {
  font-size: 15px;
  border: none;
  background-color: var(--main-color);
  border-radius: 10px;
  color: white;
  padding: 7px;
  cursor: pointer;
  transition: ease 0.5s;
}

.method_buttons button.active {
  background-color: #dfd2d2;
  color: black;
  cursor: default;
}

.method_buttons button.inactive:hover {
  transform: scale(1.1);
}

/* toggles */
.delivery_field.inactive {
  display: none;
}
.delivery_field.active {
  display: block;
}
.pickup_field.active {
  display: block;
}
.pickup_field.inactive {
  display: none;
}

.location_one.active {
  display: block;
}
.location_one.inactive {
  display: none;
}

.location_two.active {
  display: block;
}
.location_two.inactive {
  display: none;
}

.delivery_field p {
  font-size: 20px;
  display: flex;
  gap: 10px;
  align-items: center;
}
.delivery_field span {
  font-size: 20px;
}

.pickup_field p {
  font-size: 20px;
}

.payment_details p {
  font-size: 20px;
  display: flex;
  gap: 10px;
}

#payment_fullname {
  font-weight: bold;
}
#payment_contact {
  font-weight: bold;
}

#payment_address {
  font-weight: bold;
}

/* media */

@media (max-width: 500px) {
  .checkout_card p {
    font-size: 20px;
  }
  .checkout_items #item p {
    font-size: 10px;
    text-align: center;
  }

  .checkout_header p {
    font-size: 10px;
    text-align: center;
  }
  .checkout_total p {
    font-size: 20px;
  }
  .checkout_total #total {
    font-size: 20px;
    font-weight: bolder;
  }
}
