.register {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  z-index: 100;
  background: var(--background-gradient);
  display: flex;
  align-items: center;
  justify-content: center;
}

.register_field {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  color: white;
  width: 720px;
  padding: 40px;
  background-color: var(--main-color);
  border-radius: 20px;
  gap: 20px;
}
.register_field form {
  display: flex;
  flex-direction: column;
  max-width: 500px;
  width: 100%;
  height: auto;
  gap: 10px;
}

.register_field input::-webkit-outer-spin-button,
.register_field input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}

.register_field form input {
  height: 15px;
  padding: 10px;
  border-radius: 4px;
  border: none;
}

.register_field #registerBtn {
  border: none;
  width: 120px;
  background-color: var(--secondary-color);
  padding: 12px;
  color: white;
  font-size: 22px;
  border-radius: 12px;
  cursor: pointer;
  transition: all ease-in-out 0.2s;
}
.register_field form #registerBtn:hover {
  transform: scale(1.1);
}
