.about_page {
  position: relative;
  overflow: auto;
  background: var(--background-gradient);
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px;
}

.about_container {
  display: flex;
  height: 100%;
  width: 100%;
  justify-content: center;
  align-items: center;
}

.about_content {
  display: flex;
  width: 100%;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 40px;
}

.about_content video {
  border-radius: 100%;
  width: 100%;
  height: 100%;
  max-width: 490px;
  max-height: 490px;
}

.about_text {
  display: flex;
  flex-direction: column;
  text-align: center;
  max-width: 620px;
  width: 100%;
}

.about_text h1 {
  color: #fff;
}

.about_text h3 {
  color: #fff;
  line-height: 35px;
  letter-spacing: 1px;
}
