.home_container {
  position: relative;
  background: var(--background-gradient);
  height: 100%;
  width: 100%;
  z-index: 0;
  padding: 5% 0 10% 0;
}
.home_swiper {
  display: flex;
  width: 100%;
  height: 100%;
  justify-content: center;
  align-items: center;
}

.cardWithText {
  display: flex;
  flex-wrap: wrap-reverse;
  justify-content: space-around;
  align-items: center;
}
.image_container img {
  max-width: 1090px;
  min-width: 220px;
  width: 100%;
  border-radius: 20px;
}
.image_container {
  max-width: 600px;
  width: 100%;
  height: auto;
}

.info_container {
  display: flex;
  flex-wrap: wrap;
  max-width: 880px;
  height: 100%;
  width: 100%;
  text-align: center;
  justify-content: space-around;
}

.info_container p {
  font-size: 30px;
  color: white;
}

.info_container h1 {
  font-size: 50px;
  color: white;
}

.info_container button {
  font-size: 30px;
  background-color: var(--main-color);
  border: none;
  border-radius: 10px;
  color: #fff;
  padding: 20px;
  cursor: pointer;
  transition: ease-in-out 0.3s;
}

.info_container button:hover {
  transform: scale(1.1);
}

.home_swiper .swiper-button-next,
.home_swiper .swiper-button-prev {
  background: tomato;
  padding: 10px 15px;
  border-radius: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #ffff;
}

.home_swiper .swiper-pagination-bullet {
  padding: 5px;
}
.home_swiper .swiper-pagination-bullet-active {
  background-color: rgb(206, 6, 6);
}

@media (max-width: 550px) {
  .info_container p {
    font-size: 20px;
  }
  .home_swiper .swiper-button-next,
  .home_swiper .swiper-button-prev {
    position: absolute;
    top: 65%;
    background: none;
    margin: 0;
  }
}

@media (max-width: 375px) {
  .home_swiper .swiper-button-next,
  .home_swiper .swiper-button-prev {
    display: none;
  }
}
