.products { 
  background: var(--background-gradient);
  padding: 40px;
}

.box-container {
  display: flex;
  flex-wrap: wrap;
  gap: 40px 0px;
  justify-content: space-around;
}

.eyeModal {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background: var(--background-gradient);
  z-index: 5;
  display: flex;
  align-items: center;
}
.eyeModal.active {
  opacity: 1;
  visibility: visible;
  transition: all 0.5s;
  transform: translateY(0);
}

.eyeModal.inactive {
  opacity: 0;
  visibility: hidden;
  transition: all 0.5s;
  transform: translateY(40%);
}

.eye_content {
  /* border: 2px solid black; */
  width: 100%;
  max-height: 650px;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  /* flex-wrap: wrap; */
  overflow: auto;
}

.eyeModal button {
  border: none;
  font-size: 30px;
  padding: 15px;
  border-radius: 100%;
  display: flex;
  align-items: center;
  color: var(--main-color);
  position: absolute;
  right: 20px;
  top: 20px;
  cursor: pointer;
  z-index: 4;
}

.swiper_container {
  width: 100%;
  height: 100%;
  max-width: 620px;
  max-height: 720px;
  justify-content: center;
  align-items: center;
  text-align: center;
  /* border: 2px solid rgb(87, 17, 216); */
  display: flex;
  flex-wrap: wrap;
}

.swiper_gallery {
  width: 100%;
  height: 100%;
  max-width: 520px;
  max-height: 420px;
  justify-content: center;
  align-items: center;
  text-align: center;
  /* border: 3px solid yellow; */
}

.swiper_gallery img {
  width: 100%;
  max-height: 420px;
  height: 100%;
  border-radius: 20px;
}

.swiper_thumbnail {
  width: 100%;
  height: auto;
  max-width: 520px;
  /* max-height: 200px; */
  justify-content: center;
  align-items: center;
  text-align: center;
}

.swiper_thumbnail img {
  width: 100%;
  height: 150px;
  border-radius: 20px;
}
.swiper-slide-thumb-active {
  transform: scale(0.8);
  transition: all 0.2s;
}

.eye_info {
  display: flex;
  text-align: center;
  align-items: center;
  max-width: 820px;
  width: 100%;
  /* border: 3px solid yellow; */
}

.eye_info p {
  font-size: 40px;
  color: #ffff;
}

.swiper_gallery .swiper-button-next,
.swiper_gallery .swiper-button-prev {
  background: tomato;
  padding: 10px 15px;
  border-radius: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #ffff;
}

@media (max-width: 1024px) {
  .eye_content {
    flex-wrap: wrap;
  }
}

@media (max-width: 580px) {
  .eyeModal button {
    transform: scale(0.8);
  }
  .eye_info p {
    font-size: 20px;
   
  }
}
