}

.navigationBar {
  position: relative;
  padding: 10px 20px;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  box-shadow: var(--box-shadow);
  height: 7rem;
  background: linear-gradient(90deg, #ea4b11, #a15832);
  z-index: 1;
}

.icons {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}
.icon {
  cursor: pointer;
  padding: 20px;
  font-size: 30px;
  text-align: center;
  color: var(--main-color);
  transition: 0.5s ease;
}

.icons button {
  border: none;
  border-radius: 100%;
  margin: 0px 1px;
}

.icon:hover {
  transform: rotate(360deg);
}

#cart {
  position: relative;
}

#cart span {
  position: absolute;
  top: 10%;
  right: 15%;
  background-color: var(--main-color);
  padding: 2.5px 5px;
  border-radius: 100%;
  color: #fff;
}

.logo {
  cursor: pointer;
  transition: ease 0.5s;
}

.logo:hover {
  transform: scale(1.1);
  transition: ease 0.5s;
}

.logo img {
  width: 100%;
}

.navMenu button.active {
  background: var(--main-color);
}

.navMenu {
  display: flex;
  width: 100%;
  justify-content: center;
  text-align: center;
}

.navMenu button {
  font-size: 2rem;
  padding: 0.5rem 1rem;
  border-radius: 0.5rem;
  border: none;
  background: none;
  color: #fff;
  text-decoration: none;
  transition: 0.5s ease;
  cursor: pointer;
}

.navMenu button:hover,
.navMenu button:active {
  color: white;
  background-color: var(--main-color);
}

.menu {
  display: none;
}

/* media queries */
@media (min-width: 1800px) {
  .navMenu button {
    font-size: 40px;
    margin: 0 20px;
  }
}

@media (max-width: 1250px) {
  .navMenu button {
    font-size: 2rem;
    padding: 0.5 1rem;
  }
}
@media (max-width: 1180px) {
  .logo img {
    width: 80%;
  }
  .navMenu {
    display: none;
  }
  button.menu {
    display: block;
  }
}

@media (max-width: 750px) {
  .logo img {
    width: 100%;
  }
}

@media (max-width: 550px) {
  .icon {
    font-size: 20px;
    padding: 10px;
  }
  .icons button {
    margin: 0;
  }

  #cart span {
    top: 0;
    right: 0;
    padding: 2px 4px;
  }
}
@media (max-width: 380px) {
  .icon {
    font-size: 20px;
    padding: 5px;
  }
  .icons button {
    margin: 0;
  }
}
