.box {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  border-radius: 10px;
  background-color: #fff;
  padding: 40px;
  text-align: center;
}
.image-container img {
  width: 400px;
  height: 400px;
  border-radius: 20px;
}

#addCart {
  border: none;
  display: flex;
  font-size: 25px;
  text-align: center;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  background: var(--main-color);
  color: #ffff;
  border-radius: 10px;
  padding: 10px;
  transition: ease-in-out 0.2s;
}

#addCart:hover {
  transform: scale(1.1);
  transition: ease-in-out 0.2s;
}

.box-info h3 {
  font-size: 40px;
  color: var(--main-color);
  margin: 10px 0 20px 0
}

.image-container {
  position: relative;
  overflow: hidden;
}

.image-container button {
  position: absolute;
  border: none;
  font-size: 40px;
  display: flex;
  border-radius: 100%;
  padding: 10px;
  color: var(--main-color);
  top: 2%;
  right: 5%;
  cursor: pointer;
  transition: ease-in-out 0.2s;
  transform: translateX(200px);
}

.image-container button:hover {
  color: #fff;
  background-color: var(--secondary-color);
  transition: ease-in-out 0.2s;
}

.box:hover .image-container button {
  transform: translateX(0);
}

.box-price {
  background-color: #dfd2d2;
  border-radius: 40px;
  margin: 10px 0;
}

.box-price h4 {
  font-size: 30px;
  color: var(--main-color);
  margin: 0;
}

@media (max-width: 550px) {
  .image-container button {
    font-size: 20px;
  }
  .image-container img {
    width: 100%;
    height: auto;
  }
  .box {
    width: 100%;
  }
}
