.user_page {
  position: relative;

  height: 100%;
  background: var(--background-gradient);
  padding: 20px;
}


.profile_field {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  width: 100%;
  gap: 20px;
}
.profile_info img {
  width: 100%;
  max-width: 300px;
  border-radius: 100%;
}
.profile_info p {
  color: var(--main-color);
  font-size: 30px;
  font-weight: bold;
}

.profile_info span { 
  cursor: pointer;
  transition: all .2s;
}
.profile_info span:hover { 
  cursor: pointer;
  text-decoration: underline;
  transform: scale(1.1);
}

.profile_info {
  display: flex;
  flex-direction: column;
  width: 300px;
  text-align: center;
  justify-content: center;
  background-color: #fff;
  padding: 4px;
  border-radius: 20px;
}

.order_field {
  width: 100%;
  height: 300px;
  position: relative;
  background-color: #fff;
  border-radius: 20px;
  overflow-x: auto;
  text-align: center;
  padding: 10px;
}

.order_field p {
  font-size: 20px;
  font-weight: bold;
}

.transaction_field {
  width: 500px;
  height: 500px;
  display: flex;
  position: relative;
  flex-direction: column;
  align-items: center;
  background-color: #fff;
  border-radius: 20px;
  /* display: none; */
}

.transaction_field p {
  font-size: 20px;
  font-weight: bold;
}

.cancel_btn {
  border: none;
  background: none;
  text-decoration: underline;
  cursor: pointer;
  font-size: 15px;
  transition: all 0.5s;
}

.cancel_btn:hover {
  background-color: #b62929;
  padding: 4px;
  border-radius: 4px;
  text-decoration: none;
  color: #fff;
}

table {
  width: 100%;
  border: 1px solid black; 
  border-collapse: collapse;
}

table th,
table td {
  border: 1px solid black; 
}
